import React from 'react'
import { Card, Button, CardHeader, CardBody } from 'reactstrap';

import { validateEmail, clear, validateFields, customValidation } from '../../common.js'
import axios from '../../API.js'

class Requester extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			id: '1', requester: '', title: '', description: '', reward: '', timeallotted: '', isError: false, isUpdate: false,
			error: { requester: '', title: '', description: '', reward: '', timeallotted: '' }
		}
	}
	onChange = (event) => {
		const data = this.state
		data[event.target.name] = event.target.value
		this.setState({ data })
		if (data.isError)
			this.onValidation(data)
	}
	onValidation = (ref = '') => {
		const emptyFields = [
			{ key: 'requester', errVal: 'Enter Requester' },
			{ key: 'title', errVal: 'Enter Title' },
			{ key: 'description', errVal: 'Enter Description' },
			{ key: 'reward', errVal: 'Enter Reward' },
			{ key: 'timeallotted', errVal: 'Enter Reward' }
		]
		const data = validateFields(ref === '' ? this.state : ref, emptyFields)

		this.setState({ data })
		return data.isError
	}
	onSubmit = () => {
		const data = this.state
		if (!this.onValidation()) {
			if (this.state.isUpdate) {
				let data = this.state
				data.action = 'update'
				data.userid = '1'
				axios.post("/Requesters/Update", data).then(res => {
					if (res.data.status === 'success') {
						this.props.history.push('/Requesters')
						alert('Requester Details Updated Successfully..!!')
					}
				})
			}
			else {
				let data = this.state
				data.workertype = 'work1'
				data.userid = '1'
				axios.post("/Requesters/Add", data).then(res => {
					console.log(res)
					if (res.data.status === 'success')
						this.props.history.push('/Requesters')
					alert('Requester Details Added Successfully..!!')
				}).catch(err => {
					console.log(err)
				})
			}
		}
		// 	if(!res.isError) {
		// 		axios.post(this.props.common.serverURL+"/axios/Users/Add",data).then(res=>{
		// 			if(res.data.status==='success')
		// 				this.props.history.push({pathname:'/workerRegister',state:res.data.data.insertId})
		// 		}).catch(err=>{
		// 			console.log(err)
		// 		})
		// 	}
		// })
	}

	componentDidMount = () => {
		console.log(this.props.location.state)
		if (this.props.location.state !== undefined) {
			axios.get("/Requesters", { params: { id: this.props.location.state.id } }).then(res => {
				let view = res.data.data[0];
				this.setState({ id: view.ID, requester: view.RequesterName, title: view.Title, description: view.Description, reward: view.Reward, timeallotted: view.TimeAllotted, isUpdate: true })
			})
		}
		else {
			axios.get("/Requesters/Auto").then(res => {
				this.setState({ id: res.data[0].id })
			})
		}
	}

	render() {
		let propsTitle = !this.state.isUpdate ? "Add" : "Update";

		let view = (
			<>
				<div className="menubar">

					<div className="row text-left">
						<div className="col-2"></div>
						<div className="col-8">
							<Card>
								<CardHeader tag="h4">{propsTitle} Requester</CardHeader>
								<CardBody className="padding-20 font-weight-bold">

									<div className="form-group">
										<label>Requester ID</label>
										<input name="id" type="text" className="form-control" disabled value={this.state.id} />
									</div>
									<div className="form-group">
										<label>Requester Name</label>
										<input name="requester" type="text" className="form-control" required onChange={this.onChange} value={this.state.requester} />
										{this.state.error.requester}
									</div>
									<div className="form-group">
										<label>Title</label>
										<input name="title" type="text" className="form-control" required onChange={this.onChange} value={this.state.title} />
										{this.state.error.title}
									</div>
									<div className="form-group">
										<label>Description</label>
										<textarea name="description" type="text" className="form-control" required onChange={this.onChange} value={this.state.description} ></textarea>
										{this.state.error.description}
									</div>
									<div className="form-group">
										<label>Reward</label>
										<input name="reward" type="text" className="form-control" required onChange={this.onChange} value={this.state.reward} />
										{this.state.error.reward}
									</div>
									<div className="form-group">
										<label>Time Allotted</label>
										<input name="timeallotted" type="number" className="form-control" required onChange={this.onChange} value={this.state.timeallotted} />
										{this.state.error.timeallotted}
									</div>

									<div className="text-right">
										<Button color="primary" onClick={this.onSubmit}>Submit</Button>{'  '}
										<Button color="secondary" onClick={this.clear}>Clear</Button>
									</div>

								</CardBody>
							</Card>
						</div>
						<div className="col-2"></div>
					</div>
				</div>

			</>
		)
		return view;
	}
}
export default Requester;